

@import url('https://fonts.googleapis.com/css?family=Teko');

button {
  border: none;
  background: none;
  background-color: #ffd8d8;
  width: 60%;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 15px 0;
}

body {
  background-image: -webkit-gradient( linear, left top, right top, from(#141414), to(#2b2b2b));
  background-image: -webkit-linear-gradient( left, #141414, #2b2b2b);
  background-image: -o-linear-gradient( left, #141414, #2b2b2b);
  background-image: linear-gradient( to right, #141414, #2b2b2b);
  color: #cecece;
}

.App {
  text-align: center;
  background-image: url("./img/gjon-mili-stroboscopic-09.jpg");
  background-repeat: no-repeat;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  height: 100vh;
  min-height: 1500px;
}

.App-header div {
  background-color: rgba(0, 0, 0, 0.5);
  width: 75%;
}
.App-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  color: white;
  font-weight: 500;
  height: 50%;
  max-height: 325px;
}

.App-header .intro{
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(232, 44, 12, 0.7)), to(rgba(255, 0, 0, 0.7)));
  background-image: -webkit-linear-gradient(rgba(232, 44, 12, 0.7), rgba(255, 0, 0, 0.7));
  background-image: -o-linear-gradient(rgba(232, 44, 12, 0.7), rgba(255, 0, 0, 0.7));
  background-image: linear-gradient(rgba(232, 44, 12, 0.7), rgba(255, 0, 0, 0.7));
  width: 100%;
  padding: 20px 0;
  margin-bottom: 20px;
}

.App-title {
  font-size: 3em;
  font-family:'Teko', sans-serif;
  font-weight: 900;
}
.App-title  .london {
  color: #FF0000;
}

.App-title span {
  color: #E82C0C;
}

.App-intro {
  font-size: large;
}

.App-header #find-address{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  -ms-align-items: center;
  -ms-flex-align: center;
      align-items: center;

}

.enter-address {
  width: 59%;
  text-align: center;
  font-weight: 600;
  margin-bottom: 10px;
}

main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-align-items: center;
  -ms-flex-align: center;
      align-items: center;
}

#map {
  height: calc(100vh / 1.5);
}

#map div{
  border-radius: 2%;
}

/*********************** Infobox Googlemaps ******************/

.theatre-container-infobox {
  margin:0;
  border-radius: 10px;
  width: 200px;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
}
.image-container-infobox {
  margin:0;
  margin-top: 20px;
  height: 100px;
  width: 200px;
  overflow: hidden;
}
.theatre-picture-infobox {
  margin:0;
  width: 200px;
  border-radius: 3% 3% 0;
}
.theatre-info-infobox {
    width: 200px;
    overflow: hidden;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#E82C0C), to(#FF0000));
    background-image: -webkit-linear-gradient(#E82C0C, #FF0000);
    background-image: -o-linear-gradient(#E82C0C, #FF0000);
    background-image: linear-gradient(#E82C0C, #FF0000);
}
.name-infobox {
    font-family: 'Teko', sans-serif;
    font-size: 2em;
    margin:0;
    margin-top: 10px;
    color: black;
}
.address-infobox {
  font-weight: 600;
}

.theatres {
  width: 75%;
  margin-top: 20px;
}

.theatres hr, .theatre-info-infobox hr {
  /* https://css-tricks.com/examples/hrs/ */
  border: 0;
  height: 1px;
  background-image: -webkit-gradient(linear, left top, right top, from(rgba(221, 221, 221, 0)), color-stop(rgba(221, 221, 221, 0.75)), to(rgba(221, 221, 221, 0)));
  background-image: -webkit-linear-gradient(left, rgba(221, 221, 221, 0), rgba(221, 221, 221, 0.75), rgba(221, 221, 221, 0));
  background-image: -o-linear-gradient(left, rgba(221, 221, 221, 0), rgba(221, 221, 221, 0.75), rgba(221, 221, 221, 0));
  background-image: linear-gradient(to right, rgba(221, 221, 221, 0), rgba(221, 221, 221, 0.75), rgba(221, 221, 221, 0));
}

.theatre-container:first-of-type {
  margin:0;
  padding: 0;
}
.theatre-container {
  margin:0;
  margin-top: 20px;
  background-color: black;
  border-radius: 10px;
  width: 100%;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  display: none;
}

.theatre-info {
    width: 100%;
    overflow: hidden;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#E82C0C), to(#FF0000));
    background-image: -webkit-linear-gradient(#E82C0C, #FF0000);
    background-image: -o-linear-gradient(#E82C0C, #FF0000);
    background-image: linear-gradient(#E82C0C, #FF0000);
}

.theatre-info hr{
  z-index: 3;
  margin:0;
  background-image: none;
    /* https://css-tricks.com/examples/hrs/ */
    height: 12px;
    border: 0;
    -webkit-box-shadow: inset 0 12px 12px -12px rgba(0, 0, 0, 0.5);
            box-shadow: inset 0 12px 12px -12px rgba(0, 0, 0, 0.5);
}
.theatre-info .address{
  margin:0;
  margin-top: -9px;
  font-size: 0.8em;
  color: black;
  background-color: #e80000;
}

.theatre-info .name{
  font-family: 'Teko', sans-serif;
  font-size: 2em;
  margin:0;
  color: black;
}

.theatre-description .info{
  margin:0;
  font-size: 0.9em;
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: #000;
  width: 100%;
  border-radius: 10px;
}
.theatre-description .info p, .theatre-description .info div{
  margin-left: 20px;
  width: 80%;
  text-align: left;
}

.image-container:first-of-type {
  margin:0;
}
.image-container {
  margin:0;
  margin-top: 20px;
  height: 100px;
  overflow: hidden;
}


.theatre-picture {
  margin:0;
  width: 100%;
  border-radius: 3% 3% 0;
}

/* ---------  Modal only appears if you are too far away from London ------------------------------ */
#modal, #api-handling {
  position: absolute;
  display: none;
  background: rgba(0, 0, 0, 0.8);
  width: 90%;
  height: calc(100vh / 1.5);
}
#modal p {
  margin: 0;
}
#modal-content {
  width: 100%;
  margin-top: 50%;
}
.attention{
  width: 100%;
  padding: 20px 0;
  margin-bottom: 20px;
}

#modal button{
  margin-top: 20px;
  background-color: white;
}

@media (min-width: 701px) {
  .App {
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: start;
        justify-content: flex-start;
    background-size: cover;
    width: 100%;
  }
  .App-header {
    width: 100%;
    margin-top: 2px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }
  .App-header div {
    width: 100%;
    margin: 0;
    padding: 0;
    background-color: transparent;
  }
  .App-header .intro{
    width: 50%;
    padding: 20px 0;
    height: calc(100vh /5);
  }
  main {
    width: 52%;
    position: absolute;
    margin-top: 135px;
    margin-left: 49%;
  }
  #map {
    width: 100%;
    height: calc((100vh /5) * 4);
  }
  .theatres {
    margin: 0;
    max-height: calc(68vh * 4/5);
    overflow: scroll;
    position: absolute;
    margin-top: calc((100vh /5) + 44px);
    -webkit-transform: translate(-50vw, 0);
        -ms-transform: translate(-50vw, 0);
            transform: translate(-50vw, 0);
    width: calc(50vw - 4px);
  }
  .theatre-container {
    margin: 0;
    background: rgba(0,0,0,0.8);
    height: calc(66.6667vh);
  }
  #modal, #api-handling {
    position: absolute;
    display: none;
    background: rgba(0, 0, 0, 0.8);
    width: 90%;
    height: calc((100vh /5) * 4);
  }
}



